:root {
  --content-width: 1336px;
  --gutter: 12px;
  --mob-padding: 20px;
  --primary-text-color: #ffffff;
  --active-text-color: #c89afc;
  --primary-bg-color: #333333;
  --second-bg-color: #121212; }

/* roboto-regular - cyrillic */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/roboto-cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

/* roboto-500 - cyrillic */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/roboto-cyrillic-500.woff2") format("woff2"), url("../fonts/roboto-cyrillic-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

/* roboto-700 - cyrillic */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/roboto-cyrillic-700.woff2") format("woff2"), url("../fonts/roboto-cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

.container {
  max-width: calc(var(--content-width));
  padding: 0 var(--mob-padding);
  margin: 0 auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1); }

* {
  box-sizing: border-box; }

html {
  font-family: "Roboto"; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: var(--primary-text-color);
  background-color: var(--primary-bg-color); }

ul {
  padding: 0; }

.scroll-hidden {
  overflow: hidden; }

.button {
  color: var(--primar-text-color);
  border: none;
  font-size: 16px;
  border-radius: 50px;
  padding: 10px 30px;
  cursor: pointer; }

.timer {
  margin-bottom: 218px; }
  @media (max-width: 992px) {
    .timer {
      margin-bottom: 175px; } }
  @media (max-width: 640px) {
    .timer {
      margin-bottom: 125px; } }
  .timer__item {
    margin: 0 var(--gutter);
    padding: 20px 30px;
    background-color: var(--second-bg-color);
    display: flex;
    flex-direction: column;
    align-items: center; }
    @media (max-width: 992px) {
      .timer__item {
        padding: 15px 25px; } }
    @media (max-width: 640px) {
      .timer__item {
        padding: 10px 10px; } }
  .timer__number {
    font-weight: 700;
    font-size: 36px;
    line-height: 140%; }
    @media (max-width: 992px) {
      .timer__number {
        font-size: 24px; } }
    @media (max-width: 640px) {
      .timer__number {
        font-size: 18px; } }
  .timer__unit {
    margin: 0;
    opacity: 0.4; }
    @media (max-width: 992px) {
      .timer__unit {
        font-size: 18px; } }
    @media (max-width: 640px) {
      .timer__unit {
        font-size: 12px; } }

.hamburger {
  display: none;
  position: relative;
  width: 25px;
  height: 20px;
  z-index: 9;
  cursor: pointer;
  user-select: none;
  transition: all 0.4s ease-in;
  color: #ffffff;
  user-select: none; }
  @media (max-width: 992px) {
    .hamburger {
      display: block; } }
  .hamburger__line {
    position: absolute;
    top: 9px;
    left: 0;
    width: 100%;
    height: 2.4px;
    border-radius: 5px;
    background-color: #ffffff;
    z-index: 8;
    transition: all 0.3s ease; }

.header__hamburger:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2.4px;
  border-radius: 5px;
  background-color: #ffffff;
  transition: 0.3s ease; }

.header__hamburger:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.4px;
  border-radius: 5px;
  background-color: #ffffff;
  transition: 0.3s ease; }

.toggle .hamburger__line {
  display: none; }

.hamburger.toggle::after {
  transform: rotate(-45deg) translate(2px, -10px); }

.hamburger.toggle::before {
  transform: rotate(45deg) translate(3px, 11px); }

span.rotate {
  margin-right: 30px;
  transform: rotate(90deg); }

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 15;
  color: var(--primary-text-color);
  transition: 0.3s; }
  .header__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
    position: relative;
    flex-wrap: wrap;
    max-width: 1920px;
    padding: 20px 4%;
    margin: 0 auto; }
  @media (max-width: 1200px) {
    .header__logo {
      margin-right: 15px;
      order: 1; } }
  @media (max-width: 1200px) {
    .header__box {
      width: 100%;
      order: 3; } }
  @media (max-width: 992px) {
    .header__nav {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: rgba(0, 0, 0, 0.4);
      z-index: 5;
      transition: transform 0.5s ease-in;
      transform: translateX(-100%); } }
  @media (max-width: 992px) {
    .header__nav_active {
      transform: translateX(0);
      overflow-y: auto; } }
  .header__menu {
    display: flex; }
    @media (max-width: 1200px) {
      .header__menu {
        justify-content: center; } }
    @media (max-width: 992px) {
      .header__menu {
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        padding-top: 50px;
        flex-direction: column;
        justify-content: center;
        background-color: #2d303a;
        width: 72vw; } }
  .header__menu-item {
    position: relative;
    list-style-type: none; }
    .header__menu-item:hover .submenu {
      background-color: grey;
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0); }
      @media (max-width: 992px) {
        .header__menu-item:hover .submenu {
          background-color: transparent;
          transform: none; } }
    .header__menu-item > a {
      padding: 28px;
      display: block;
      color: currentColor;
      text-decoration: none;
      transition: 0.3s; }
      .header__menu-item > a:hover {
        color: var(--active-text-color); }
      @media (max-width: 992px) {
        .header__menu-item > a {
          font-size: 24px; } }
    .header__menu-item .submenu {
      width: 250px;
      position: absolute;
      top: 100%;
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
      transform: translate(0, -20px); }
      @media (max-width: 992px) {
        .header__menu-item .submenu {
          width: 100%;
          position: static;
          visibility: visible;
          opacity: 1;
          transform: none; } }
      .header__menu-item .submenu > .header__menu-item:hover .submenu {
        display: block; }
      .header__menu-item .submenu > .header__menu-item > a {
        width: 100%; }
        .header__menu-item .submenu > .header__menu-item > a:hover {
          background-color: #504d4d; }
        @media (max-width: 992px) {
          .header__menu-item .submenu > .header__menu-item > a {
            font-size: 16px;
            padding: 14px 14px 14px 35px; } }
      .header__menu-item .submenu .submenu {
        display: none;
        position: absolute;
        left: 100%;
        top: 0; }
        @media (max-width: 992px) {
          .header__menu-item .submenu .submenu {
            display: block;
            position: static;
            padding: 14px 14px 0 30px; } }
  .header__button {
    color: currentColor;
    background: linear-gradient(94.26deg, #c89afc 9.51%, #7c6afa 90.23%);
    font-weight: 400; }
    @media (max-width: 1200px) {
      .header__button {
        margin: 0 15px;
        order: 2; } }

.main {
  flex-grow: 1; }

.promo {
  background: center/cover no-repeat url(../img/promo_bg.png); }
  .promo__inner {
    padding: 270px 0 0; }
  .promo__title {
    font-weight: bold;
    font-size: 64px;
    line-height: 140%; }
    @media (max-width: 992px) {
      .promo__title {
        font-size: 48px; } }
    @media (max-width: 640px) {
      .promo__title {
        font-size: 26px;
        margin-bottom: 40px; } }
  .promo__wrap {
    background-color: var(--second-bg-color); }
  .promo__box {
    padding: 35px 0 28px;
    display: flex;
    justify-content: space-between; }
    @media (max-width: 1200px) {
      .promo__box {
        flex-direction: column; } }
  .promo__holder {
    display: flex;
    justify-content: space-between;
    width: 40%; }
    @media (max-width: 1200px) {
      .promo__holder {
        margin-bottom: 50px;
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-column-gap: 40px;
        justify-content: center;
        width: 100%; } }
    @media (max-width: 640px) {
      .promo__holder {
        grid-template-columns: auto;
        grid-template-rows: repeat 2, auto;
        grid-row-gap: 40px; } }
  .promo__button {
    background: linear-gradient(94.78deg, #df5950 11.19%, #451046 93.72%); }
  .promo__counts {
    display: grid;
    grid-row-gap: 10px; }
  .promo__count {
    display: flex;
    justify-content: space-between; }
  .promo__count-name {
    opacity: 0.4; }
  .promo__count-number {
    padding-left: 30px; }
  .promo__wrapper {
    width: 50%; }
    @media (max-width: 1200px) {
      .promo__wrapper {
        padding: 0 40px;
        width: 100%; } }
    @media (max-width: 640px) {
      .promo__wrapper {
        padding: 0; } }
  .promo__earned {
    margin-bottom: 8px; }
    @media (max-width: 640px) {
      .promo__earned {
        margin-bottom: 30px;
        display: grid;
        grid-template-rows: repeat(2, auto);
        grid-row-gap: 20px;
        justify-content: center; } }
  .promo__earned-name {
    opacity: 0.4; }
  .promo__earned-count {
    padding-left: 30px; }
    @media (max-width: 640px) {
      .promo__earned-count {
        text-align: center;
        padding-left: 0; } }
  .promo__progress {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .promo__line-wrap {
    margin-bottom: 5px;
    height: 6px;
    width: 100%;
    background-color: #ffffff; }
  .promo__line {
    width: 65%;
    height: 6px;
    background: linear-gradient(94.78deg, #df5950 11.19%, #451046 93.72%); }
  .promo__numbers {
    width: 100%;
    display: flex;
    justify-content: space-between; }
  .promo__price {
    opacity: 0.4; }

.footer {
  flex-shrink: 0; }
